import React, { useState } from 'react';
import './footer.css';



const Footer = () => {

    const [inputValue, setInputValue] = useState('');
    const onChangeInput = (e) => {
        setInputValue(e.target.value);
    };

  const [selectValue, setSelectValue] = useState('');
  const onChangeSelect = (e) => {
    setSelectValue(e.target.value);
  };

  return (
    <div className="w_footer">
        <div className="w_footer-con">
            <div className="W_logo">
                <img src="/w_images/logo.png"></img>
            </div>
            
           <div className="w_text">
                <p> <i class="fa-solid fa-minus"></i>  <i class="fa-solid fa-minus"></i>  <i class="fa-solid fa-minus"></i>  <i class="fa-solid fa-minus"></i> / <i class="fa-solid fa-minus"></i> </p>
                <p>   CHK 10 <i class="fa-solid fa-minus"></i>  1544-1234</p>
                <p> CHK() <i class="fa-solid fa-minus"></i>  123-45-67890 <i class="fa-solid fa-minus"></i>     CHK()</p>
                <p>copyright &#169; CHK cinema all right reserved.</p>
            </div>
        

            <div className="w_office">
                <select value={selectValue} onChange={onChangeSelect}>
                    <option value="A">===    ===</option>
                    <option value="B">CHK </option>
                    <option value="C">CHK </option>
                    <option value="D">CHK </option>
                    <option value="F">CHK </option>
                </select>  
                <button type="submit" value={inputValue} onChange={onChangeInput}>Go!</button>
            </div>
        </div>
        
     
    
    </div>
  )
}

export default Footer