import React from 'react'
import './snack.css';


const Combo = () => {
  return (
    <div className="w_s-p">
        <div className="w_combox">
          <img className="w_pop"src="w_images/CHKcombo.jpg"/>
          <p>CHK </p>
          <p className="w_combo-sub">( 1L + 2M ) </p>
          <p>6,000  <i class="fa-solid fa-minus"></i> 350kcal</p> 
        </div>
        <div className="w_combox">
          <img className="w_pop"src="w_images/sset.jpg"/>
          <p> </p>
          <p className="w_combo-sub">( 1M + 1M )</p>
          <p>4,500  <i class="fa-solid fa-minus"></i> 250kcal</p> 
        </div>
        <div className="w_combox">
          <img className="w_pop"src="w_images/lset.jpg"/>
          <p> </p>
          <p className="w_combo-sub">( 2L + 2L )</p>
          <p>10,500  <i class="fa-solid fa-minus"></i> 1000kcal</p> 
        </div>
    </div>
  )
}

export default Combo